.field {
    width: 400px;
    height: 400px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: 40px auto;
}

.field__cell {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 40px;
    height: 40px;
    border: 1px solid cadetblue;
}

.field__cell_empty {
    background-color: #73f3ff;
}

.field__cell_damaged {
    background-color: #dccd4c;
}

.field__cell_destroyed {
    background-color: #dc3c21;
}

.field__cell_ship {
    background-color: rgba(80, 118, 54, 0.9);
}

.field__cell_miss {
    background-color: #a2a2a2;
}



